import React, { Component } from 'react';
import { Row, Form, Col } from 'antd';
import FilterPathEducate from 'components/common/input/FilterPathEducate';
import { BtnSearch, BtnClear } from 'components/common/button';

@Form.create({ name: 'formSerchIndividual' })
class FormSerchIndividual extends Component {
  state = {};
  handleReset = () => {
    this.props.form.resetFields();
  };
  handleSearch = e => {
    const { formDataSearch } = this.props;
    e.preventDefault();
    this.props.form.validateFields((err, values) => {
      formDataSearch(values);
    });
  };
  render() {
    return (
      <Form className="form-inline-label" layout="vertical" onSubmit={this.handleSearch}>
        <Row gutter={24}>
          <FilterPathEducate form={this.props.form} />
        </Row>
        <Row>
          <Col span={24} style={{ textAlign: 'right' }}>
            <div className="btn-group">
              <BtnSearch htmlType="submit" />
              <BtnClear onClick={this.handleReset} />
            </div>
          </Col>
        </Row>
      </Form>
    );
  }
}

export default FormSerchIndividual;



